<script>
import ListTitle from "@/components/ListTitle";
import PicList from "@/components/PicList";
import PicText from "@/components/PicText";
import Radio from "@/components/Radio";
import Loading from "@/components/Loading";
export default {
  name: "Recommend",
  components: {
    ListTitle,
    PicList,
    PicText,
    Radio,
    Loading,
  },
  data() {
    return {
      banner:[],
      recom_list: [],
      newsong_list: [],
      radio_list: [],
      radio_hot:[],
      radio_new:[],
      radio_bo:[],
      slice_list: [],//裁切数组
      start: 3,
      rotate:false
    };
  },
  created() {
    this.$axios.get("/personalized").then((data) => {

      this.recom_list = data.data.result.slice(0, 10);

    });

    this.$axios.get("/personalized/newsong").then((d) => {
      // 所有数据
      this.newsong_list = d.data.result.map((newd) => {
        newd.al = newd.song.album;
        newd.ar = newd.song.album.artists;
        return newd;
      });
      // console.log(this.newsong_list)
    //  初始数据
      this.slice_list = this.newsong_list.slice(0, 3);

    });
   // 热门电台
    this.$axios.get("/dj/toplist?type=hot&limit=20").then((art) => {
      this.radio_hot = art.data.toplist;
      this.radio_list  = this.radio_hot;
    });
    // 新晋榜
     this.$axios.get("/dj/toplist?type=new&limit=24").then((a) => {
      this.radio_new = a.data.toplist;
      // console.log(this.radio_new)
    }); 
    this.$axios.get("/banner?type=2").then((aa) => {
     this.banner = aa.data.banners;
      console.log(this.banner);
    }); 
  


  },

  methods: {
    // 切换数据
    toggleNew(){
      let end = this.start + 3;
      this.rotate = true   
      if (end < 10) {
        this.slice_list = this.newsong_list.slice(this.start, end);
      } else {
        this.start = 0;
        end = 3;
        this.slice_list = this.newsong_list.slice(this.start, end);
      }
      this.start = end;

      // 让rotate数据500ms后改为false
      setTimeout(()=>{
        this.rotate = false;
      },500)
    }
  }
};
</script>
