import Vue from 'vue'
import VueRouter from 'vue-router'
import Recommend from '@/views/Recommend.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/recommend',
  },
  {
    path: '/recommend',
    name: 'Recommend',
    meta:{
      hide:true
    },
    component: Recommend,
  },

  {
   path:'/recommend_songs/:id',
   props:true,
   meta:{
    hideNav:true
  },
  component:()=>import('@/views/RecommendSongs')
  },

  // 歌手榜
  {
    path: '/artists',
    name: 'Artists',
    meta:{
      hideNav:true
    },
    component:()=>import('@/views/Artists')
  },
// 排行榜
  {
    path: '/toplist',
    name: 'TopList',
    meta:{
      hideNav:true
    },
    component:()=>import('@/views/TopList')
  },
  // 歌单
  {
    path: '/musiclist',
    name: 'MusicList',
    meta:{
      hideNav:true
    },
    component:()=>import('@/views/MusicList')
  },
  

  // 电台播放
  {
    path:'/radio_play/:radId',
    meta:{
      hideNav:true, //隐藏导航栏
      hidePlay:true //隐藏歌曲播放器
    },
    props:true,
   component:()=>import('@/views/RadioPlay')
   },
  

  {
    path: '/search',
    name: 'Search',
    meta:{
      hideNav:true
    },
    component: () => import( '@/views/Search')
  },
  {
    path: '/concert',
    name: 'Concert',
    component: () => import('@/views/Concert'),
  }
]

const router = new VueRouter({
  routes
})

export default router
