<template>
  <router-link tag="div" :to="`/recommend_songs/${item.id}`">  
  <div class="piclist">
    <img class="img" :src="item.picUrl" />
      <img class="play" src="../assets/img/play2.png">
    <p>{{ item.name }}</p>
    <label><i>{{ item.playCount|formatCount }}</i></label>

  </div>
    </router-link>

</template>

<script>
export default {
  props: ["item"],
  filters: {
    formatCount(val) {
      if (val / 10000 > 10000) {
        return parseInt(val / 100000000) + "亿";
      } else {
        return parseInt(val / 10000) + "万";
      }
    },

  },
};
</script>

<style lang="less" scoped>
// @color:rgb (174, 206, 137);
.piclist {
  position: relative;
  z-index: -333;
  margin-bottom: 16px;
  font-size: 13px;
  .img {
    border-radius: 10px;    
  }
  .play{
      position: absolute;
      right: 10px;
      bottom: 50px;
      width:20px
  }
  p {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    padding: 6px 2px 0 6px;
    min-height: 30px;
    line-height: 1.2;
  }
  label{
    border-radius: 10px;
    padding: 3px;
    background-color: rgba(105, 105, 109, 0.6);
    color: white;
    position: absolute;
    left: 0px;
    top: 0px;
  i {
    background: url(../assets/img/ear.png)no-repeat 0 0;
    padding-left: 18px;
    background-size: 15px 15px;
  }
}
 }
</style>

