<template>
  <div v-if="playMusic" :class="['lyricpage', { pauseLy: !playstate }]">
    <div
      class="lyric-bg"
      :style="`background-image:url(${playMusic.picUrl})`"
    ></div>
    <!-- 将close事件名称传给播放页面的lyricscreen组件 -->
    <div @click="$emit('close'),showList=false" class="back">
      <img src="../assets/img/back.png" />
    </div>
    <div class="content">
      <!-- 顶部栏 -->
      <div class="navbar" v-if="playMusic">
       <div> <p>{{ playMusic.name }}</p> </div>
        <p v-for="artis in playMusic.ar" :key="artis.id">
          {{ artis.name }}
        </p>
      </div>
      <!-- 图片栏 -->
      <div v-show="!showWords" @click="showWords = true" class="img">
        <img :src="playMusic.picUrl" />
      </div>

      <!-- 歌词栏 -->
      <div
        v-show="showWords"
        v-if="lyric"
        @click="showWords = false"
        class="lyric"
      >
        <ul :style="`transform:translateY(-${20 * lyricIndex - 50}px)`">
          <li
            v-for="(words, index) in lyricArray"
            :key="index"
            :class="{ hightLight: index == lyricIndex }"
          >
            {{ words.lyric }}
          </li>
        </ul>
      </div>
      <!-- 播放条 -->
      <div class="range">
        <p>
          <span>{{ currentTime | formatTime }}</span>
          <input
            type="range"
            max="100"
            @change="pullRange"
            :value="(currentTime / duration) * 100"
          />
          <span>{{ duration | formatTime }}</span>
        </p>
        <div class="bts">
          <div class="line1">
            <div class="songMode" @click="$emit('changeMode')">
              <img src="../assets/img/shun.png" v-show="playMode == 1" />
              <img src="../assets/img/sui.png" v-show="playMode == 2" />
              <img src="../assets/img/dan.png" v-show="playMode == 3" />
            </div>
            <!-- 评论图标 -->
            <div @click="showrecom = true" class="recom">
             <svg t="1620789613092" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="4361" width="20" height="20"><path d="M952.888889 796.444444h-256l-82.488889 162.133334c-2.844444 5.688889-8.533333 5.688889-11.377778 2.844444L355.555556 796.444444h-284.444445c-28.444444 0-56.888889-14.222222-56.888889-56.888888V108.088889c0-28.444444 22.755556-51.2 51.2-51.2h896c28.444444 0 51.2 22.755556 51.2 51.2v637.155555c-5.688889 31.288889-17.066667 51.2-59.733333 51.2z m0-682.666666h-881.777778v625.777778h312.888889l199.111111 142.222222 71.111111-142.222222h298.666667V113.777778z m-227.555556 398.222222c-31.288889 0-56.888889-25.6-56.888889-56.888889s25.6-56.888889 56.888889-56.888889 56.888889 25.6 56.888889 56.888889-25.6 56.888889-56.888889 56.888889z m-227.555555 0c-31.288889 0-56.888889-25.6-56.888889-56.888889s25.6-56.888889 56.888889-56.888889 56.888889 25.6 56.888889 56.888889-25.6 56.888889-56.888889 56.888889z m-230.4 0c-31.288889 0-56.888889-25.6-56.888889-56.888889s25.6-56.888889 56.888889-56.888889 56.888889 25.6 56.888889 56.888889-25.6 56.888889-56.888889 56.888889z" fill="#E7F3AE" p-id="4362"></path></svg>
              <i>{{ hotComments.length + comments.length }}</i>
            </div>
           <!-- 歌单图标 -->
            <div @click="showList = !showList">
              <svg t="1620790122248" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="9857" width="25" height="25"><path d="M964.608 234.496c-46.08-52.565333-104.789333-93.696-169.642667-118.784a34.030933 34.030933 0 0 0-46.421333 31.744v420.352a178.005333 178.005333 0 0 0-110.933333-38.741333c-98.816 0-179.2 80.384-179.2 179.2S538.794667 887.466667 637.610667 887.466667s179.2-80.384 179.2-179.2c0-1.877333-0.170667-3.754667-0.341334-5.632 0-1.024 0.341333-1.877333 0.341334-2.901334V201.216c36.181333 20.309333 69.12 46.933333 96.597333 78.165333 12.458667 14.165333 34.133333 15.530667 48.128 3.072 14.165333-12.288 15.530667-33.792 3.072-47.957333zM637.610667 819.2c-61.098667 0-110.933333-49.834667-110.933334-110.933333s49.834667-110.933333 110.933334-110.933334 110.933333 49.834667 110.933333 110.933334-49.834667 110.933333-110.933333 110.933333zM185.344 307.2h392.533333c18.773333 0 34.133333-15.36 34.133334-34.133333s-15.36-34.133333-34.133334-34.133334h-392.533333a34.133333 34.133333 0 1 0 0 68.266667zM424.277333 460.8h-238.933333c-18.773333 0-34.133333 15.36-34.133333 34.133333s15.36 34.133333 34.133333 34.133334h238.933333c18.773333 0 34.133333-15.36 34.133334-34.133334s-15.36-34.133333-34.133334-34.133333zM321.877333 682.666667h-136.533333c-18.773333 0-34.133333 15.36-34.133333 34.133333s15.36 34.133333 34.133333 34.133333h136.533333c18.773333 0 34.133333-15.36 34.133334-34.133333s-15.36-34.133333-34.133334-34.133333z" p-id="9858" fill="#E7F3AE"></path></svg>
            </div>
          </div>

          <div class="line2">
            <div @click="pre"><img src="../assets/img/pre.png" alt="" /></div>
            <div class="play" @click="$emit('changePlay', playstate)"></div>
            <div @click="next"><img src="../assets/img/next.png" alt="" /></div>
          </div>
        </div>

        <!-- 歌单 -->
         <transition enter-active-class="animate__animated animate__fadeInUp" 
                    leave-active-class="animate__animated animate__fadeOutDown">
        <ul class="songlist" v-show="showList">
          <li
            v-for="(song, i) in songList"
            :key="song.id"
            :class="{ hightLight: playMusic.id == song.id }"
            @click="$emit('changeSong', song)"
          >
            {{ i + 1 }}. {{ song.name }} - {{ song.al.name }}
          </li>
          <li @click="showList=false">{{songList.length>0? " 关闭" :"歌单空空如也~"}}</li>
        </ul>
         </transition>
        <!-- 评论 -->
        <transition enter-active-class="animate__animated animate__fadeInUp" 
                    leave-active-class="animate__animated animate__fadeOutDown">
        <div class="recomlist" v-show="showrecom">
          <svg @click="showrecom=false" t="1620788071533" class="icon" viewBox="0 0 1024 1024"  version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1865">
            <path d="M512 736c-9.6 0-16-3.2-22.4-9.6l-384-384c-12.8-12.8-12.8-32 0-44.8s32-12.8 44.8 0L512 659.2 873.6 297.6c12.8-12.8 32-12.8 44.8 0s12.8 32 0 44.8l-384 384c-6.4 6.4-12.8 9.6-22.4 9.6z" fill="#AECE89"  p-id="1866"
            ></path>
          </svg>

          <recom-detail
            :hotComments="hotComments"
            :comments="comments"
          ></recom-detail>
        </div>
        </transition >
      </div>
    </div>
  </div>
</template>
<script>
import RecomDetail from "@/components/RecomDetail";
export default {
  name: "LyricScreen",
  props: [
    "playMusic",
    "playstate",
    "lyric",
    "currentTime",
    "duration",
    "songList",
    "playMode",
  ],
  data() {
    return {
      showWords: false,
      showList: false,
      songMode: null,
      hotComments: "",
      comments: "",
      showrecom: false,
    };
  },
  components: {
    RecomDetail,
  },

  // 歌曲评论
  watch: {
    playMusic() {
      // console.log(event);
      this.$axios
        .get("/comment/music?id=" + this.playMusic.id)
        .then((music) => {
          this.comments = music.data.comments;
          this.hotComments = music.data.hotComments;

        });
    },
  },

  methods: {
    pre() {
      let i = 0;
      this.songList.map((data, index) => {
        if (data.id == this.playMusic.id) {
          i = index;
        }
      });
      if (i <= 0) {
        i = this.songList.length - 1;
      } else if (this.playMode == 1) {
        //顺序播放
        i--;
      } else if (this.playMode == 2) {
        //随机播放时
        i = Math.floor(Math.random() * this.songList.length);
      }
      console.log(i);
      let listSong = this.songList[i];
      this.$emit("changeSong", listSong);
    },
    next() {
      let i = 0;
      this.songList.map((data, index) => {
        if (data.id == this.playMusic.id) {
          i = index;
        }
      });
      // 到最后一首时跳到第一首
      if (i >= this.songList.length - 1) {
        i = 0;
      } else if (this.playMode == 1) {
        i++;
      } else if (this.playMode == 2) {
        //随机播放时
        i = Math.floor(Math.random() * this.songList.length);
      }
      //  console.log(i)

      let listSong = this.songList[i];
      this.$emit("changeSong", listSong);
    },
    pullRange(event) {
      let val = event.target.value;
      this.$emit("pullRange", val);
      // console.log(event.target.value)
    },
  },
  computed: {
    // 将字符串歌词处理为数组
    lyricArray() {
      let lyricarr = this.lyric.split("\n");
      lyricarr.pop(); //去掉最后一项的undefined
      let reg = /\[(\d+):(\d+\.\d+)\](.*)/;
      let newArr = lyricarr.map((d) => {
     
        if (reg.test(d)) {
          let lyObj = {
            time: parseInt(RegExp.$1) * 60 + parseFloat(RegExp.$2),
            lyric: RegExp.$3,
          };
          return lyObj;
        }
      });
   
      return newArr;
    },
    // 获取当前播放歌词下标
    lyricIndex() {
      let index = 0;
      let words = this.lyricArray;
      for (let i = 0; i < words.length; i++) {
        if (i >= words.length - 1) {
          index = i;
          break;
        }
        if (
          words[i].time <= this.currentTime &&
          words[i + 1].time > this.currentTime
        ) {
          index = i;
          break;
        }
      }
      return index;
    },
  },

  filters: {
    formatTime(val) {
      let min = parseInt(val / 60);
      let sec = parseInt(val % 60);
      min = min < 10 ? "0" + min : min;
      sec = sec < 10 ? "0" + sec : sec;

      return min + ":" + sec;
    },
  },
};
</script>

<style lang="less" scoped>
.lyricpage {
  width: 100vw;
  height: 100vh;
  color: #ccc;
  background-color: rgb(182, 178, 173);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 111;
  //设置模糊背景
  .lyric-bg {
    transform: scale(1.5);
    transform-origin: center center 0px;
    filter: blur(40px);
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: auto 100%;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    height: 100%;
    overflow: hidden;
    z-index: -1;
    &::before {
      content: " ";
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      background-color: rgba(0, 0, 0, 0.5);
    }
  }
  .back {
    margin: 15px;
    width: 25px;
  }
  // 内容
  .content {
    color: rgb(226, 226, 226);
    // 顶部栏
    .navbar {
      text-align: center;
      padding: 5px;
      margin: 10px auto;
      div{
         width:10em;
          word-break:keep-all; 
          white-space:nowrap; 
          overflow:hidden;
          text-overflow:ellipsis;

          margin: 0 auto;
      p{  
        font-size: larger;
          animation: move 6s 0s infinite linear;     
      }
      }
     
      
    }
    // 图片栏
    .img {
      width: 40vh;
      height: 55vh;
      margin: 20px auto;
      img {
        width: 100%;
        border-radius: 50%;
        animation: circle 10s 0s infinite linear;
      }
    }
    // 歌词框
    .lyric {
      height: 55vh;
      margin: 20px auto;
      // background-color: #fff;
      text-align: center;
      overflow: hidden;
      font-size: 14px;
      ul {
        li {
          margin: 10px;
        }
      }
    }
    // 进度条
    .range {
      p {
        display: flex;
        justify-content: space-around;
        margin: 10px 0;
        [type="range"] {
          width: 70vw;
          margin-top: 8px;
          -webkit-appearance: none;
          background: #ddd;
          height: 3px;
          &::-webkit-slider-thumb {
            -webkit-appearance: none;
            height: 13px; /*拖动块高度*/
            width: 13px; /*拖动块宽度*/
            border-radius: 50%; /*外观设置为圆形*/
            background-color: rgb(174, 206, 137);
          }
        }
      }

      .bts {
        .line1 {
          display: flex;
          justify-content: space-around;
          margin: 15px 0;
          .recom {
            padding: 5px;
            position: relative;
            i {
              position: absolute;
              top: -5px;
              right: -3px;
              font-size: 14px;
              color: white;
            }

          }
          .songMode {
            img {
              width: 22px;
            }
          }
        }
        .line2 {
          display: flex;
          justify-content: space-around;
          margin: 10px 0;
          div {
            width: 32px;
            text-align: center;
            line-height: 65px;
          }
          .play {
            position: relative;
            &::before {
              content: "";
              background: url("../assets/img/paused.png") no-repeat 0 0 / cover;
              position: absolute;
              top: 0px;
              left: -7px;
              width: 40px;
              height: 40px;
              z-index: 0;
            }
          }
        }
      }
      // 歌单
      .songlist {
        border-radius: 20px 20px 0 0;
        color: #444;
        width: 100vw;
        background-color: rgb(248, 248, 248);
        position: absolute;
        bottom: 0px;
        // left: 0;
        z-index: 1111;
        li {
          margin: 15px;
          &:last-child {
            text-align: center;
          }
        }
      }

      // 评论界面
      .recomlist {
        overflow: hidden;
        height: 80vh;
        width: 100vw;
        position: absolute;
        bottom: 0px;
        z-index: 2222;
        svg{
          position: absolute;
          top: 10px;
          left: 20px;
          width:25px;
            height:25;
        }
        div {
          height: 100%;
          background-color: rgb(248, 248, 248);
          overflow-x: hidden;
          overflow-y: auto;
          color: #333;
        }
      }
    }
  }
}

// 暂停样式
.pauseLy {
  .content {
    .img {
      img {
        animation-play-state: paused;
      }
    }
    .range {
      .bts {
        .line2 {
          .play {
            &::before {
              content: "";
              background: url("../assets/img/playing.png") no-repeat 0 0 / cover;
            }
          }
        }
      }
    }
  }
}

// 歌词高亮
.hightLight {
  color: rgb(165, 201, 125);
  font-size: large;
}

// 转起来
@keyframes circle {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
// 走起来
@keyframes move {
  from{
    transform: translateX(-10em);
  }
  to{
    transform: translateX(10em);
  }
}
</style>