<template>
  <div class="comment">
    <div class="top">
      <i>评论区：{{ hotComments.length + comments.length }}</i>
      <p>
        <span @click="New" :class="i == 0 ? 'color' : ''">最新 </span>|
        <span @click="Hot" :class="i == 1 ? 'color' : ''" > 精彩</span>
      </p>
    </div>

    <ul v-for="(item, index) in hotdata" :key="index">
      <div>
        <li>
          <img :src="item.user.avatarUrl" alt="" />
        </li>
        <li>
          <p>{{ item.user.nickname }}</p>
          <p>{{ item.time | formatDate("Y年m月d日") }}</p>
          <p>
            {{ item.content }}
          </p>
        </li>
      </div>
      <div>
        <i
          ><span>{{ item.likedCount | formatNum() }} </span
          ><img src="../assets/img/zan.png" alt=""
        /></i>
      </div>
    </ul>
  </div>
</template>
<script>
export default {
  props: ["hotComments", "comments"],
  data() {
    return {
      hotdata: [],
      i: 0,
    };
  },
  methods: {
    Hot() {
      this.hotdata = this.hotComments;
      this.i = 1;
    },
    New() {
      this.hotdata = this.comments;
      this.i = 0;
    },
  },
  watch: {
    comments(event) {
      // console.log(event);
      this.hotdata = event;
    },
  },
  filters: {
    formatDate(value, fstr) {
      let d = new Date(value);
      let formatO = {
        "Y+": d.getFullYear(),
        "m+": d.getMonth() + 1,
        "d+": d.getDate(),
        "h+": d.getHours(),
        "M+": d.getMinutes(),
        "S+": d.getSeconds,
      };
      for (let k in formatO) {
        let reg = new RegExp(k);
        let val = formatO[k];
        val = val < 10 ? "0" + val : val;
        fstr = fstr.replace(reg, val);
      }
      return fstr;
    },
    formatNum(value) {
      if (value >= 10000) {
        return (parseInt(value) / 10000).toFixed(0) + "万";
      }
      return value;
    },
  },
};
</script>
<style lang="less" scoped>
.color {
  color: rgb(174, 206, 137);
}
.comment {
   font-size: 14px;
  border-radius: 20px 20px 0 0;
  .top {
    display: flex;
    justify-content: space-around;
    padding: 10px;
    & > p {
      span {
        padding: 5px;
      }
    }
  }

  ul {
    padding: 5px;
    display: flex;
    div {
      &:first-child {
        width: 80%;
        display: flex;
        li {
          &:first-child {
            width: 40px;
            height: 40px;
            text-align: center;
            box-sizing: border-box;
            border-radius: 50%;
            overflow: hidden;
            img {
              width: 100%;
              height: 100%;
            }
          }
          &:nth-child(2) {
            padding: 5px;
            width: 80%;

            p {
              &:first-child{
                color: #888;
              }
              &:nth-child(2) {
                font-size: 12px;
                margin: 5px 0;
                color: #aaa;
              }
              &:last-child {
                margin: 5px 0;
                font-size: 14px;
                width: 100%;
                line-height: 1.8;
                word-wrap: break-word;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
              }
            }
          }
        }
      }
      &:last-child {
        width: 20%;
        padding-top: 5px;
        text-align: center;
        i {
          color: rgb(174, 206, 137);
          line-height: 80px;
          img {
            color: rgb(174, 206, 137);

            padding-top: 5px;
            width: 22px;
          }
        }
      }
    }
  }
}
</style>