<template>
  <!-- 当状态为暂停时，添加暂停时的样式 -->
  <div :class="['player', { pause: !playstate }]">
    <!-- audio -->
    <audio
      v-if="playMusic"
      autoplay
      @play="play"
      @pause="pause"
      @ended="ended"
      @timeupdate="timeupdate"
      ref="player"
      :src="`https://music.163.com/song/media/outer/url?id=${playMusic.id}.mp3`"
    ></audio>

    <!-- 底部播放器 -->
    <transition enter-active-class="animate__animated animate__backInLeft">
    <div class="minibar" v-if="playMusic">
      <div><img :src="playMusic.picUrl" /></div>
      <div @click="isFull = true">
        <p>{{ playMusic.name }}</p>
        <p>
          {{ playMusic.al.name}} —
          <span v-for="art in playMusic.ar" :key="art.id">{{ art.name }}</span>
        </p>
      </div>
     
      <!-- 播放暂停-旋转进度条按钮 -->
      <div class="bts" @click.stop="togglePlay">
        <canvas width="50" height="50" ref="playcircle"></canvas>
      </div>
    </div>
 </transition>
    <!-- 全屏歌词屏幕 -->
    <transition leave-active-class="animate__animated animate__bounceOutRight">
      <lyric-screen
        v-show="isFull"
        @close="isFull = false"
        @changePlay="togglePlay"
        @pullRange="pullRange"
        @changeSong ="$emit('changeSong',$event)"
        @changeMode="changeMode"
        :playMusic="playMusic"
        :playstate="playstate"
        :lyric="lyric"
        :currentTime="currentTime"
        :duration="duration"
        :songList="songList"
        :playMode ="playMode"
        ref="lyricpage"
      ></lyric-screen>
    </transition>
  </div>
</template>
<script>
import LyricScreen from "@/components/LyricScreen";
export default {
  props: ["playMusic" ,"songList"],
  components: {
    LyricScreen,
  },
  data() {
    return {
      duration: 0,
      currentTime: 0,
      playstate: true,
      isFull: false,
      lyric:null,
      playMode:1 //播放模式：顺序，随机，单曲
    };
  },
  watch:{
    
    // 侦听当前播放音乐的变化
     playMusic(newMusic){
   
     this.axios.get('/lyric?id='+newMusic.id).then(d=>{
   
     this.lyric = d.data.lrc.lyric;

     })
     }
  },
  methods: {
    
    ended(){
     if(this.playMode == 3){
       this.$refs.player.play();
     }else{
       this.$refs.lyricpage.next();
     }
    },
     // 切换播放模式
    changeMode(){
      if(this.playMode>=3){
       this.playMode = 1;
      }else{
      this.playMode++;
      }
     
    },
    timeupdate(event) {
      this.duration = event.target.duration;
      this.currentTime = event.target.currentTime;
      this.drawCicle(this.duration, this.currentTime);
    },
    //  画圈圈
    drawCicle(total, current) {
      let canvas = this.$refs.playcircle;
      let cxt = canvas.getContext("2d");
      // 画外圈
      cxt.clearRect(0, 0, 50, 50);
      cxt.beginPath();
      cxt.strokeStyle = "rgb(150, 187, 107)";
      cxt.lineWidth = 2;
      cxt.arc(25, 25, 20, 0, Math.PI * 2);
      cxt.stroke();
      cxt.closePath();

      // 画内部动态圈
      cxt.beginPath();
      cxt.strokeStyle = "rgb(150, 187, 107)";
      cxt.lineWidth = 5;
      cxt.arc(25, 25, 19, 0, Math.PI * 2 * (current / total));
      cxt.stroke();
      cxt.closePath();
    },
    togglePlay() {
      if (this.playstate) {
        this.playstate = false;
        this.$refs.player.pause();
      } else {
        this.playstate = true;
        this.$refs.player.play();
      }
      
    },
    play() {
      this.playstate = true;
      
    },
    pause() {
      this.playstate = false;
    },
    // 拖动滑块改变时间
    pullRange(value){
    let time = value/100*this.duration;//得到拖动之后的当前时间
    this.$refs.player.currentTime = time;
    // console.log(time)
    }
  },
};
</script>
<style lang="less" scoped>

.player {
  
  .minibar {
    position: fixed;
    width: 100%;
    height: 60px;
    background-color: rgb(227, 247, 175);
    bottom: 0px;
    z-index: 9;
    display: flex;
    align-items: center;
    font-size: 14px;
    div {
      &:first-child {
        //图片
        width: 40px;
        position: relative;
        margin-left: 10px;
        &::after {
          content: "";
          display: block;
          position: absolute;
          z-index: -3;
          width: 50px;
          height: 50px;
          background-color: rgb(150, 187, 107);
          top: -5px;
          left: -5px;
          border-radius: 50%;
        }

        img {
          width: 100%;
          height: 40px;
          border-radius: 50%;
          animation: rotate 4s 0s infinite linear;
        }
      }
      &:nth-child(2) {
        flex: 1;
        text-indent: 20px;
        p {
          // 超出一行省略号
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        width: 60vw;
          &:last-child {
            font-size: 12px;
            color: #444;
          }
        }
      }
      &:last-child {
        //播放暂停按钮
        z-index: 22;
        margin-right: 20px;
        position: relative;
        // line-height: 60px;
        &::before {
          content: "";
          background: url("../assets/img/pause.png") no-repeat 0 0 / cover;
          position: absolute;
          top: 13px;
          left: 13px;
          width: 25px;
          height: 25px;
          z-index: 0;
        }
      }
    }
  }
}
// 暂停样式
.pause {
  .minibar {
    div {
      &:first-child {
        img {
          animation-play-state: paused;
        }
      }
      &:last-child {
        &::before {
          content: "";
          background: url("../assets/img/play11.png") no-repeat 0 0 / cover;
          position: absolute;
          top: 15px;
          left: 17px;
          width: 20px;
          height: 20px;
        }
      }
    }
  }
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>