<template>
  <div id="app">
    <!-- 当为search时 就隐藏导航栏 !hideNav为false 则隐藏-->
    <Nav v-show="!$route.meta.hideNav"></Nav>
    <div class="padTop" v-if="!$route.meta.hideNav"></div>


    <Player
      v-show="!$route.meta.hidePlay"
      :playMusic="playMusic"
      :songList="songList"
      @changeSong="changeSong"
    ></Player>

    <!-- <keep-alive> -->
      <router-view @playing="playing" @addList="addList" :playMusic="playMusic" />
    <!-- </keep-alive> -->
  </div>
</template>

<script>
import Nav from "@/components/Nav";
import Player from "@/components/Player";
import defaultPic from "@/assets/img/music.jpg";

export default {
  components: {
    Nav,
    Player,
  },
  data() {
    return {
      playMusic: null,
      songList: [],
      songstate:""
    };
  },
  methods: {
    // 歌单中切换歌曲
    changeSong(song) {
      if (song) {
        this.playMusic = song;
      }
    },
    playing(event) {
      // console.log(event);
      if (!event.picUrl && event && event.al) {
        event.picUrl = event.al.picUrl;
      }
      if (!event.picUrl) {
        event.picUrl = defaultPic;
      }
      this.playMusic = event;
      // console.log(event.name)
    },
    addList(eve) {

      let isAdd = true;
      this.songList.map((data) => {
        if (data.id == eve.id) {
          // 不加歌单
          isAdd = false;
        }
      });

      // 如果数据是数组
      if(isAdd){
     if (Array.isArray(eve)) {
        this.songList = eve.map((d) => {
      //  console.log(this.songList)
         
          return d;
        });
        // 当全部播放时，自动播放第一首
        this.playMusic = this.songList[0];
      } else {
        // this.songList.push(eve); //加入歌单
      }
      }
      
     
    },
  },
};
</script>
<style lang="less" scoped>
#app {
  // background-color: rgb(231, 243, 174);
  .padTop {
    padding-top: 100px;
  }
}
</style>
